.person__name { display: block; font-family: "AvenirNext-DemiBold", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 14px; color: #343558; }

.person__location { display: block; text-transform: uppercase; }

.press-slider { overflow: hidden; }

.press-slider__slide { font-family: "AvenirNext-Italic", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 18px; color: #000; }

.press-slider__nav { margin-bottom: 30px; font-size: 0; position: relative; }

.press-slider__btn { display: inline-block; vertical-align: middle; height: 30px; width: 25%; box-sizing: border-box; padding: 0 10px; background: none; text-align: center; position: relative; }
.press-slider__btn:before { content: ""; position: absolute; top: 0; left: 0; width: 100%; height: 100%; background: #FFF; opacity: 0.8; transition: all 0.15s linear; pointer-events: none; }
.press-slider__btn img { max-width: 100%; max-height: 100%; }
.press-slider__btn:hover:before, .press-slider__btn.is-active:before { opacity: 0; visibility: hidden; }

.press-slider__arrow { position: absolute; top: 100%; left: -100px; font-size: 7px; color: #93C4D2; width: 64px; margin: 10px 0 0 -32px; transition: left 0.3s ease; }


